/* eslint-disable eqeqeq */
/* eslint-disable no-console */
/* eslint-disable vue/require-prop-types */
import { mapGetters } from 'vuex'
export default {
  props: ['token'],

  data () {
    return {
      routeName: 'rooms',
      massage: {
        name: '',
        fee: 0,
        time: 0
      },
      sauna: {
        name: '',
        fee: 0
      }
    }
  },

  computed: {
    ...mapGetters(['allMassage', 'allsteamsauna'])
  },

  methods: {
    Clear () {
      this.massage.name = ''
      this.massage.fee = 0
      this.massage.time = 0

      this.sauna.name = ''
      this.sauna.fee = 0
    },

    async newMassagePackage () {
      const config = {
        headers: { 'X-CSRF-TOKEN': this.token }
      }
      const bodyParameters = this.massage
      try {
        await this.$axios
          .$post('newMassagePackage', bodyParameters, config)
          .then(({ result, message }) => {
            if (result == true) {
              this.Clear()
              this.$toast.success(message)
              this.$emit('refresh')
            }
          })
          .catch((errors) => {
            console.log(errors.response)
            if (errors.response.data.message) {
              this.$toast.error(
                this.$ObjTemplate.trim(errors.response.data.message)
              )
            } else {
              this.$toast.error(
                this.$ObjTemplate.trim(errors.response.request.statusText)
              )
            }
          })
      } catch (e) {
        console.log(e)
      }
    },

    async newSteamSaunaPackage () {
      const config = {
        headers: { 'X-CSRF-TOKEN': this.token }
      }
      const bodyParameters = this.sauna
      try {
        await this.$axios
          .$post('newSteamSaunaPackage', bodyParameters, config)
          .then(({ result, message }) => {
            if (result == true) {
              this.Clear()
              this.$toast.success(message)
              this.$emit('refresh')
            }
          })
          .catch((errors) => {
            console.log(errors.response)
            if (errors.response.data.message) {
              this.$toast.error(
                this.$ObjTemplate.trim(errors.response.data.message)
              )
            } else {
              this.$toast.error(
                this.$ObjTemplate.trim(errors.response.request.statusText)
              )
            }
          })
      } catch (e) {
        console.log(e)
      }
    },

    async delMassage (id) {
      const config = {
        headers: { 'X-CSRF-TOKEN': this.token }
      }
      try {
        await this.$axios
          .$post('delMassage', { id }, config)
          .then(({ result, message }) => {
            if (result == true) {
              this.$toast.success(message)
              this.$emit('refresh')
            }
          })
          .catch((errors) => {
            console.log(errors.response)
            if (errors.response.data.message) {
              this.$toast.error(
                this.$ObjTemplate.trim(errors.response.data.message)
              )
            } else {
              this.$toast.error(
                this.$ObjTemplate.trim(errors.response.request.statusText)
              )
            }
          })
      } catch (e) {
        console.log(e)
      }
    },

    async delSauna (id) {
      const config = {
        headers: { 'X-CSRF-TOKEN': this.token }
      }
      try {
        await this.$axios
          .$post('delSauna', { id }, config)
          .then(({ result, message }) => {
            if (result == true) {
              this.$toast.success(message)
              this.$emit('refresh')
            }
          })
          .catch((errors) => {
            console.log(errors.response)
            if (errors.response.data.message) {
              this.$toast.error(
                this.$ObjTemplate.trim(errors.response.data.message)
              )
            } else {
              this.$toast.error(
                this.$ObjTemplate.trim(errors.response.request.statusText)
              )
            }
          })
      } catch (e) {
        console.log(e)
      }
    }
  }
}
